* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.title {
  margin-bottom: 13%;
  position: relative;
  font-size: 300%;
  margin-top: 10%;
  top: 30%;
}


@media screen and (min-width:800px) {
  .title {
    font-size: 500%;
  }
  #div1 h2 {
    font-size: x-large;
  }
  
}